/************** DailyDetails **************/

#dailyDetailsHandlePage {
    width: 100%;
    max-width: var(--max-width);
    margin: auto;
    padding-bottom: 20px;
    transition: bottom 250ms, opacity 250ms;
    background-color: var(--background-white);
  }
  #dailyDetailsHeader {
    position: sticky;
    top: 0;
    z-index: 1;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 70px;
    background-color: var(--main-color);
  }
  #dailyDetailsBack {
    margin-left: 20px;
    width: 35px;
    height: 35px;
    fill: white;
    cursor: pointer;
  }
  #dailyDetailsBack:hover {
    fill: black;
  }
  #dailyDetailsHeaderTitle {
    color: white;
    margin: 0 30px;
    overflow: hidden;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
  }
  #intakeTitle {
    direction: ltr;
    text-align: center;
    font-weight: bold;
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-kinda-black);
    margin: 25px 0;
  }
  .intakePercentTitlesContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    width: 90%;
  }
  .intakePercentTitlesContainer p {
    margin-bottom: 5px;
    color: #5d6fd7;
    font-weight: bold;
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  }
  .intakePercentContainer {
    position: relative;
    width: 90%;
    height: 14px;
    margin: auto;
    margin-bottom: 25px;
    background-color: #eaeaea;
    border-radius: 15px;
  }
  .intakePercentContainer div {
    position: absolute;
    right: 0;
    bottom: calc(50% - 5px);
    height: 10px;
    width: 0%;
    max-width: calc(100% - 4px);
    margin-right: 2px;
    border-radius: 5px;
    background-image: linear-gradient(-225deg, #775ed8 0%, #b0b3ff 100%);
    transition: width 350ms;
  }
  #infoTitle {
    font-weight: bold;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-kinda-black);
    margin: 50px 13px 5px 0px;
  }
  #dailyDetailsNutritionInfoDetail {
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
    background-color: var(--background-white);
    color: var(--text-kinda-black);
  }
  #dailyDetailsNutritionInfoTitles {
    width: 50%;
  }
  .dailyDetailsNutritionInfoMain {
    font-weight: bold;
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
    padding: 0 13px;
  }
  .dailyDetailsNutritionInfoSub {
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
    margin-top: 2px;
    margin-bottom: 2px;
    padding: 0 13px;
  }
  #dailyDetailsNutritionInfoValues {
    width: 50%;
  }
  #dailyDetailsNutritionInfoValues p {
    text-align: left;
  }
  